import { Suspense, lazy, memo, useContext, useEffect, useMemo } from 'react'

import AddIcon from '@mui/icons-material/Add'
import CloseIcon from '@mui/icons-material/Close'
import { Button } from '@mui/material'
import ExpenseContext from '../context/ExpenseContext'
import '../css/Expenses.css'
import { getExpenses } from '../services/ExpenseTrackerApiService'
import Expense from './Expense'
import ExpenseForm from './ExpenseForm'
import Expenses, { FetchExpenses } from './Expenses'
import ExpenseThemeContext from '../context/ThemeContext'
import ReactDOM from 'react-dom'
const ExpenseDashboard = () => {
  const context = useContext(ExpenseContext)
  const themeContext = useContext(ExpenseThemeContext)
 
 
  const modalFunction = () => {
    const modal = document.getElementById('modal')
    context.setTitle('')
    context.setAmount('')
    context.setCategories([])
	  context.setNote('')
	  modal.style.backgroundColor = (themeContext.theme)==="Dark"?"#333":"white"
    modal.style.display = 'block'
  }
  
  return (
    <div>
      <Button
        startIcon={<AddIcon />}
        variant="contained"
			  onClick={modalFunction}
			  className={themeContext.theme==="Dark"?"Dark_Button":"Normal"}
      >
        Expense
      </Button>
      <div className="modalCenter">
        <div id="modal" className="Modal" data-testid="modal">
          <Button
            color="error"
            variant="contained"
            startIcon={<CloseIcon />}
            onClick={() => {
              {
                modal.style.display = 'none'
                context.setMsg('')
              }
            }}
            data-testid="close"
            style={{ marginLeft: '85%' }}
          />
          <ExpenseForm type="add" />
        </div>
      </div>
      <Suspense fallback={<h1>Load</h1>}>
       <Expenses/>
        </Suspense>
    </div>
  )
}



export default ExpenseDashboard
