import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet'
import SearchIcon from '@mui/icons-material/Search'
import {
  AppBar,
  Box,
  IconButton,
  Input,
  InputLabel,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Switch,
  Toolbar,
  Typography,
} from '@mui/material'
import { memo, useContext, useState } from 'react'
import { Link } from 'react-router-dom'
import ExpenseContext from '../context/ExpenseContext'
import { getExpenseByTitle } from '../services/ExpenseTrackerApiService'
import { Label } from '@mui/icons-material'
import "../css/ExpenseMenuBar.css"
import ExpenseThemeContext from '../context/ThemeContext'

const ExpenseBar = () => {
  console.log("Menu Bar")
	const context = useContext(ExpenseContext)
	const themeContext = useContext(ExpenseThemeContext)
  const [search, SetSearch] = useState('')
  const onChange = (e) => {
    SetSearch(e.target.value)

    if (e.target.value === '') {
      context.update(context.expenseArrayCopy)
    }
  }
  const searchExpense = (e) => {
    console.log(search)
    if (e.key === 'Enter') {
      const expenseList = getExpenseByTitle(
        search,
        context.expense.expenseArray,
      )
      context.update(expenseList)
       
    }
  }
	
	const toggleTheme = () => {
        themeContext.setTheme(themeContext.theme === 'Normal'? 'Dark' : 'Normal');
    };
  return (
	  <Box>
		  <AppBar position="static">
			  

        <Toolbar variant="dense"  className={themeContext.theme==="Normal"?"Menu_Normal":"Menu_Dark"}>
          <IconButton
            edge="start"
            color="inherit"
            sx={{ mr: 2 }}
          >
            <AccountBalanceWalletIcon sx={{ fontSize: '40px' }} />
          </IconButton>
          <Typography variant="h6" sx={{ ml: 2, mr: 4 }}>
            Expense Tracker App
          </Typography>

          <List sx={{ display: 'flex', flexDirection: 'row' }}>
            <ListItemButton data-testid="home">
              <Link style={{ textDecoration: 'none', color: 'white' }} to={'/'}>
                <ListItem>
                  <ListItemText>Home</ListItemText>
                </ListItem>
              </Link>
            </ListItemButton>
            <ListItemButton data-testid="report">
              <Link
                style={{ textDecoration: 'none', color: 'white' }}
                to={'/report'}
              >
                <ListItem>
                  <ListItemText>Report</ListItemText>
                </ListItem>
              </Link>
            </ListItemButton>
          </List>

          <SearchIcon />

          <Input
            sx={{ border: 1, bgcolor: 'white' }}
            onChange={onChange}
            onInput={onChange}
            onKeyDown={searchExpense}
				  />
				  <InputLabel sx={{ml:5,color:"white"}}  htmlFor='switch'>Dark theme</InputLabel>
				  <Switch  color='default' id='switch' onChange={toggleTheme}/>
        </Toolbar>
      </AppBar>
    </Box>
  )
}
export default ExpenseBar
