import CloseIcon from '@mui/icons-material/Close';
import CreateIcon from '@mui/icons-material/Create';
import DeleteIcon from '@mui/icons-material/Delete';
import { Box, Button, Typography } from '@mui/material';
import { memo, useContext, useEffect, useRef } from 'react';
import ExpenseContext from '../context/ExpenseContext';
import '../css/Expenses.css';
import { deleteExpenseById } from '../services/ExpenseTrackerApiService';
import ExpenseForm from './ExpenseForm';
import {
	inputFormStyle,
	openUpdateForm,
} from '../services/ExpenseTrackerUtils';
import ExpenseThemeContext from '../context/ThemeContext';
import "../css/ExpenseForm.css"
const Expense = props => {
	
	const context = useContext(ExpenseContext);
	const themeContext = useContext(ExpenseThemeContext);
	const deleteExpense = () => {
		deleteExpenseById(props.obj.id).then(() => {
			context.setReload(!context.reload);
		});
	};

	const modal = useRef();
	const updateClick = () => {
		openUpdateForm(props, context,themeContext, modal);
	};
	const modalClose = () => {
		modal.current.style.display = 'none';
		
	};

	return (
		<div className="Expense" >
			<div  className="modalCenter">
				<div id="modal1" ref={modal} className="Modal">
					<Button
						color="error"
						variant="contained"
						startIcon={<CloseIcon />}
						onClick={modalClose}
						data-testid="close1"
						style={{ marginLeft: '85%' }}
					/>
					<ExpenseForm Object={props.obj} type="update" />
				</div>
			</div>
			<Box component={'div'} sx={inputFormStyle}>
				<div className="Expense" id="id1">
					<Button
						id="delete"
						
						variant="contained"
						size="small"
						color="error"
						data-testid="delete"
						startIcon={<DeleteIcon />}
						onClick={deleteExpense}
					/>
					<Button
						className="update"
						id="update"
						data-testid="update"
						variant="contained"
						size="small"
						startIcon={<CreateIcon />}
						onClick={updateClick}
						sx={{ ml: 1 }}
					/>
					<br />
					<Typography variant="h6" style={{ color: themeContext.theme === "Dark" ? "white" : "black " }}>
						<span className="title">
							{props.obj.title}
						</span>
					</Typography>
					<Typography variant="h6" color={'green'} fontWeight="bold" >
						<span className="amount">
							${props.obj.amount}
						</span>
					</Typography>
					<Typography variant="h6" style={{ color: themeContext.theme === "Dark" ? "white" : "black " }}>
						<span className="categories">
							{props.obj.categories}
						</span>
					</Typography>
					<Typography variant="h6" style={{color:themeContext.theme==="Dark"?"white":"black "}}>
						<span className="note">
							{props.obj.note}
						</span>
					</Typography>
				</div>
			</Box>
		</div>
	);
};

export default Expense;
